@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Open+Sans:wght@300;400;600&display=swap);
:root {
  --black: rgb(31, 31, 34);
  /* --black: #0e0e0e;*/
  --black2: rgba(28, 28, 28, 0.966);
  /* --black2: #0e0e0ef3;*/
  /* --black2: rgba(85, 88, 99, 0.966); */
  --white: #ffffff;
  --gold: rgb(200,165,106);
  --max-width: 1350px; 
}
  
* {
  box-sizing: border-box;
  margin: 0;
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 0.5rem;
  background: rgba(28, 28, 28, 0.966);
  background: var(--black2);
}

::-webkit-scrollbar-thumb {
  background: rgb(200,165,106);
  background: var(--gold);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #ffffff;
  background: var(--white);
  
}

body {
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(31, 31, 34);
  background-color: var(--black);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

button {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  padding: 0.7rem;
  border: none;
  border-radius: 5px;
}

button:hover{
  background-color: rgb(200,165,106);
  background-color: var(--gold);
}

.button {
  text-decoration: none;
  color: rgb(31, 31, 34);
  color: var(--black);
  background-color: rgb(200,165,106);
  background-color: var(--gold);
  border-radius: 2px;
  padding: .5rem 1rem;
  margin: 1rem;
  box-shadow: black 2px 3px 1px;
}

.button:hover {
  background-color: #ffffff;
  background-color: var(--white);
}
.footer {
    width: 100%;
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--gold);
    font-size: 0.8rem;
    padding: 3rem 0;
    background-color: var(--black2);
    border-top: 1px solid var(--gold)
  }
  
.reactLogo {
  font-size: 1.2rem;
  margin-left: .2rem;
}

.home-wrapper {
  width: 100%;
}

.heroSection {
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  width: 100%;
  background-color: var(--black);
  height: calc(100vh - 5rem);
}
  
.nameAboutWrapper{
  padding: 0 1rem;
}
  
.fullName {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.7rem;
  font-weight: 600;
}
  
.fullName p {
  margin: 0 0 .5rem;
  text-align: center;
}
  
.about {
  text-align: center;
  margin: 3.5rem 0 0;
}

.socialWrapper {
  padding: 4rem 1rem 0;
}

.socialWrapper a {
  margin: 1rem;
}
  
.social {
  font-size: 2.7rem;
  flex-basis: 15%;
}

.linkedin,
.github {
  color: var(--white);
  cursor: pointer;
}

.linkedin:hover,
.github:hover {
  color: var(--gold);
}

.featured-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--black);
  color: var(--white);
  padding: 0 0 5rem 0;
}

.featured-container h2 {
  font-size: 2rem;
  margin-bottom: 2.5rem;
}

.featured-container p {
  font-weight: 400;
  padding: 0 1rem;
  margin-bottom: 4rem;
}
  
@media (min-width: 411px) {

  .fullName {
    font-size: 3.4rem;
  }
}
  
@media (min-width: 768px) {

  .nameAboutWrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

  }

  .fullName p {
    font-size: 4.5rem;
    text-align: left;
  }

  .about {
    margin: 0;
    width: 17rem;
    padding-bottom: 1rem;
    text-align: right;
  }
  
  .social {
    flex-basis: 4rem;
  }
} 
  
@media (min-width: 1024px) {

  .fullName p{
    font-size: 5rem;
  }

  .about {
    width: 20rem;
    font-size: 1.2rem;   
  }

  .featured-wrapper {
    display: flex;
  }
}

@media (min-width: 1280px) {
  .fullName {
    font-size: 7.5rem;
  }
}

  
@media (min-width: 1440px) {

  .nameAboutWrapper{
    margin-top: 2rem;
  }

  .fullName p{
    font-size: 8rem;
  }

  .about {
    width: 23rem;
    font-size: 1.6rem;   
    padding-bottom: 3rem;
  }

  .socialWrapper{
    padding: 4.5rem 0 0;
  }

  .social {
    flex-basis: 5rem;
  }
}
  
@media (min-width: 1877px) {
  
  .nameAboutWrapper{
    width: 70%;
  }
}
.skills {
    color: var(--white);
    padding: 5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--black);
}

.skills h2 {
    font-size: 2rem;
    padding-bottom: 2.5rem;
}

.skills-logos {
    padding: 0 2rem;
}

/* .skills-logos img:first-child{
    padding-top: 0.5rem;
} */

.skills-logos img {
    height: 50px;
    margin: 2rem 1rem;
}

.skills-logos img:first-child {
    height: 50px;
}

@media only screen and (min-width: 768px){
    .skills-logos {
        max-width: 35rem;
    }
}

@media only screen and (min-width: 1024px) {
    .skills-logos {
        max-width: 55rem;
    }

    .skills-logos img {
        height: 80px;
        margin: 2rem 1.5rem;
        max-width: 55rem;
    }
    
    .skills-logos img:first-child {
        height: 80px;
        
    }

}
.featured {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 2rem;
    box-shadow: rgba(0, 0, 0, 0.233) 1px 3px 5px 1px, rgba(0, 0, 0, 0.233) -1px 3px 5px 1px;
}

.featured img {
    position: relative;
    width: 20rem;
    height: 20rem;
    border-radius: 5px;
}

.featured-description {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    transition: opacity .3s ease;
    opacity: 0;
    width: 20rem;
    height: 20.2rem;
    border-radius: 5px;
}

.featured-description p {
    max-width: 19rem;
    padding-bottom: 2rem;
}

.featured:hover .featured-description {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.877);
}

@media (min-width: 411px) {
    .featured img {
        width: 23rem;
        height: 23rem;
    }

    .featured-description {
        width: 23rem;
        height: 23.05rem;
    }
}

@media (min-width: 1024px) {
    .featured {
        margin: 2rem;
    }
} 


.title-container {
    margin: 6rem 0 6rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

h1 {
    font-size: 2rem;
    color: var(--white);
    font-family: 'Josefin Sans', sans-serif;
}

.icons-container {
    width: 12rem;
    font-size: 3rem;
    color: var(--white);
    margin-bottom: 1rem;
}

.icons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 1rem;
}
.projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;
    /* margin: 3rem;
    padding: 2rem;
    box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.432), inset -3px -3px 3px rgba(0, 0, 0, 0.432) ;
    border-radius: 10px; */
}

.bootcamp {
    font-size: 1.5rem;
    padding-left: .5rem;
}
.card-wrapper {    
    width: 19rem;
    height: 23rem;
    margin: 1.5rem 1rem;
    color: var(--white);
    position: relative;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.233) 1px 3px 5px 1px, rgba(0, 0, 0, 0.233) -1px 3px 5px 1px;
    background-color: rgba(0, 0, 0, 0.151);
    background-color: var(--black2);
}

.img-wrapper{
    position: relative;
    width: 100%;
}

.img-wrapper img {
    width: 100%;
    border-radius: 5px 5px 0 0 ;
}

.description-wrapper{
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 0;
    width: 100%;
    overflow: hidden;
    transition: .3s ease;
    background-color: rgba(0, 0, 0, 0.877);
    border-radius: 5px 5px 0 0;
    text-align: center;
    -webkit-transform: translate(0%, -100%);
    transform: translate(0%, -100%);
}

.card-wrapper:hover .description-wrapper {
    height: 100%;
}

.card-wrapper:hover .title {
    color: var(--gold);
}

.title {
    padding-top: 1rem;
    font-size: 1.2rem;
    color: var(--white);
}

.description {
    font-size: 1.1rem;
    padding: 0 1rem 3rem;
    
}

.project-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.github {
    width: 2rem;
    height: auto;
}

.btn {
    cursor: pointer;
}

.card-anchor,
.card-anchor:link,    
.card-anchor:visited {
    color: black;
    text-decoration: none;
}

.project-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

@media (min-width: 375px) {
    .card-wrapper {    
        width: 22.5rem;
        height: 27rem;  
    }

}

@media (min-width: 411px) {
    .card-wrapper {    
        width: 24rem;
        height: 28.55rem;  
    }

}

@media (min-width: 1024px) {
    .card-wrapper {  
        margin: 1.5rem;
    }
}
header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5rem;
    width: 100%;;
    color: var(--gold);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: var(--black2);
    border-bottom: 1px solid var(--gold);
    z-index: 1;
    font-family: 'Josefin Sans', sans-serif;
}

.nav-wrapper {
    display: flex;
    width: 90%;
    justify-content: center;
    align-items: center;
}

.home, .projects, .contact {
    width: 7rem;
    font-size:0.9rem;
    font-weight: bold;
}

.nav-wrapper div:hover {
    color: var(--white);
}

.nav-wrapper a {
    color: var(--gold);
}

.projects {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.projects > * {
    margin: 0 .2rem;
}

.open-dropdown {
    position: absolute;
    top: calc(100% + 2.1rem);
    background-color: var(--black2);
    border-left: 1px solid var(--gold);
    border-right: 1px solid var(--gold);
    border-bottom: 1px solid var(--gold);
    border-radius: 0 0 5px 5px;
    width: 12rem;
    padding: 1.5rem 0 .5rem 0;
}

.open-dropdown p {
    padding-bottom: 1.5rem;
    color: var(--white);
}

.open-dropdown p:hover {
    color: var(--gold);
}
.contact-form-container {
    width: 100%;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}

form input,
form textarea,
form button {
    width: 100%;
    padding: .5rem;
    border:none;
    font-family: 'Roboto', sans-serif;
}

form input {
    margin-bottom: 1rem;
    
}

form input:focus,
form textarea:focus,
form button:focus {
    outline: 2px solid var(--color-primary);
}

form button {
    cursor: pointer;
    border: none;
}

.success-message {
    text-align: center;
    color: var(--color-primary);
}
.contact-container {
    width: 100%;
    color: var(--color-white);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contact-wrapper {
    width: 100%;
    padding: 5rem 1rem 5rem 1rem;
    max-width: var(--max-width);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contact-container h2{
    text-align: center;
    color: var(--white);
    font-family: 'Josefin Sans', sans-serif;
    width: 100%;
    font-size: 2rem;
    border-bottom: 2px solid var(--color-primary);
    padding-bottom: .5rem;
    margin-bottom: 3rem;
}

.contact-container p{
    margin-bottom: 1.5rem;
    color: var(--white);
}

.social-media {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 1rem 0 3rem 0;
    width: 100%;
    max-width:  500px;
}

.form-wrapper {
    width: 100%;
    max-width:  500px;
}
.social-media-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 1s ease-in-out;
    flex-wrap: wrap;
}

.social-media-wrapper  a{
    margin: .5rem;
    transition: all .3s ease-in-out;
    font-weight: bold;
}

.social-media-wrapper  a:hover:not(:last-child) svg{
    fill: var(--gold);
}

.social-media-wrapper a:last-child{
    margin: 2rem auto 0 auto;
}

@media (min-width: 376px) {

    .social-media-wrapper a:last-child{
        margin: .5rem;
    }
  }

